import React, { useEffect, useState } from "react";
import { Button } from "reactstrap";

export const Repe = ({ id, pNum, pWeigth, edit }) => {
  const [weigth, setWeigth] = useState();
  const [num, setNum] = useState();
  const [isEditable, setIsEditable] = useState();

  useEffect(() => {
    setIsEditable(edit)
    setWeigth(pWeigth)
    setNum(pNum)
  }, [edit, pWeigth, pNum])

  const handleOnclickEdit=(e) => {
    setIsEditable(!isEditable)
  }
  
  return (
    <tr>
        <th scope="row">{ id }</th>
        <td>{num} ? <input value={weigth} onChange={(event) => setWeigth(event.target.value)}/>: weigth </td>
        <td>{isEditable ? <input value={weigth} onChange={(event) => setWeigth(event.target.value)}/>: weigth }</td>
        <td>{!isEditable ? <Button color="primary" onClick={handleOnclickEdit}>edit</Button>:""}</td>
        <td>{isEditable ? <Button color="danger" onClick={handleOnclickEdit}>done</Button>:""}</td>
    </tr>
  );
};